* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.App {
  background-color: black;
  color: white;
  width: 350px;
  text-align: center;
}

.personal {
  width: 80%;
  padding: 1rem;
  border-radius: 30px;
}

.nameCont h5 {
  opacity: .6;
  font-weight: 600;
  margin-bottom: 1.5rem;
}

.buttons {
  opacity: .9;
  margin: .7rem;
  display: flex;
  justify-content: center;
  gap: 1rem;
  height: 100%;
}

.div-email {
  height: 100%;
  display: flex;
}

.emailBtn {
  padding: .2rem 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background-color: white;
  color: black;
  border: none;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
}

.linkedBtn {
  padding: .2rem .9rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background-color: blue;
  color: white;
  border: none;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
}

.about-cont, .int-cont {
  opacity: .9;
  text-align: start;
  padding: 1rem 3rem;
  margin: .6rem 0;
}

.int-cont {
  padding-top: 0;
}